<template>
    <div class="bgColor">
        <div class="header">
            <img src="../../../images/failPay.png" alt="">
            <div class="curatorText">
                <img src="../../../images/curatorText05.png" alt="">
            </div>
        </div>
        <footer-text></footer-text>
    </div>

</template>

<script>
import footerText from '../../../components/footerText'
export default {
    components: {
        footerText,
  },
  data() {
      return {
          totalNumber:100,
      }
  },
  methods:{
      goBack:function(){
          this.$router.push({path:'/becomeCurator/paymentMethods'})
          //返回后台数据方法待添加
      },
      loadAdmin:function(){

      }

  }
}
</script>

<style lang="less" scoped>
    .bgColor{
        height:100%;
        min-height: 16rem;
        background:#EEEEEE;
        position: relative;
        #footer_text{
            position: absolute;
            bottom:0;
        }
    }
    .header{
        height: 7rem;
        background: #4CACFF;
        text-align: center;
        position: relative;
        img{
            width:84%;
            margin-top:14%;
        }
        .curatorText{
            position: absolute;
            width:84%;
            bottom:-1.1rem;
            left:8%;
            margin:0 auto;
            text-align: center;
            img{
                width:100%;
                margin: 0;
            }

        }
    }


    .bottom{
        width:90%;
        position: absolute;
        bottom:2%;
        left:5%;
        color:#AEAEAE;
        span{
            display:inline-block;
            text-align: center;
            font-size:.3rem;
            width:50%;
            float:left;
        }
    }
</style>



